import {React} from 'react'
import { useHistory } from 'react-router'
import Swal from 'sweetalert2'

const Landing = () =>{
    const history = useHistory()
    return(
        <>
        <div className="card px-3 py-3" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
            <div className="d-flex flex-row mb-3">
                <h5>Pilih Versi Mockup: </h5>
            </div>
            <div className="d-flex flex-row justify-content-start">
                <div onClick={()=>history.push('/ver1')} role="button" className="card px-3 py-3 mr-3 on" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                    <h5>Mockup Versi 1</h5>
                </div>
                <div onClick={()=>Swal.fire(
                    {
                        icon: <i class="fas fa-angle-right"></i>,
                        text:'To be Added'
                    }
                )} role="button" className="card px-3 py-3 on" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                    <h5>Mockup Versi 2 (Mengadopsi Modul BC30 INSW yg sudah ada)</h5>
                </div>                                
            </div>
        </div>
        </>
    )
}

export default Landing